<template>
  <v-dialog
    v-model="route_dialog"
    max-width="600px"
  >
    <article class="content-wrapper">
      <button class="close" />
      <header class="modal-header">
        <h2>There are still unsaved changes!</h2>
      </header>
      <div class="content">
        <p>
          You have not saved all changes.
          You can press "STAY" and then the update button to save all changes. Or you can ignore the changes and continue on your way with the "LEAVE" button
        </p>
      </div>
      <footer class="modal-footer">
        <v-btn
          color="green"
          @click="changesDialog(true)"
        >
          Stay
        </v-btn>
        <v-btn
          color="red"
          @click="changesDialog(false)"
        >
          Leave
        </v-btn>
      </footer>
    </article>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'

  export default {
    name: 'Dialog',
    data () {
      return {
        route_dialog: false,
      }
    },
    computed: mapState(['changes']),
    watch: {
      changes (value) {
        if (typeof value === 'object' && value.route) { this.route_dialog = true }
      },
    },
    methods: {
      changesDialog (result) {
        this.route_dialog = false

        if (!result) {
          // save it because 'changes' needs to be false to get through $route listener
          let route = this.changes.route

          this.$store.commit('setChanges', { error: false })

          this.$router.push({ name: route })
        }
      },
    },
  }
</script>

<style scoped>
    .content-wrapper {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        margin: 0;
        padding: 2.5rem;
        background-color: white;
        border-radius: 0.3125rem;
        box-shadow: 0 0 2.5rem rgba(0, 0, 0, 0.5);
    }
    .modal-header {
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        margin: 0;
        padding: 0 0 1.25rem;
    }

    .content {
        position: relative;
        display: flex;
    }

    .modal-footer {
        position: relative;
        display: flex;
